:root {
    /* main */
    --width-main: 1200px;

    /* font */
    --font-base: 'Cormorant Upright', serif;
    --font-alt: 'Open Sans', sans-serif;
    
    /* size */
    --base: 1rem;
    --sm: calc(var(--base) * 1.5);
    --med: calc(var(--base) * 3);
    --large: calc(var(--base) * 5);
  
    /*  */
    --shadow: 0 2rem 3rem #777;
  
    /* border-radius :basic 4px=0.25rem  */
    --border-radius-4px: 0.3rem;
  
    /* Animation :basic 300ms=0.3s  */
    --animation_basic-duration-300ms: 300ms;
  
    /* letterspacing : basic 0.1rem */
    --letterspacing-basic: 0.1rem;
    
      
    /* color */   
  --color-naver: #0eae44;
  --color-facebook: #3b5998;
  --color-netflix: #d61319;
  --color-kakao: #fee101;

  --color-red: #e01541;
  --color-blue: #446df6;
  --color-green: #33ca7f;

  --color-black: #000;

  --color-grey: #555;
  --color-grey-light1: #777;
  --color-grey-light2: #aaa;

  --color-white-darker: #eee;
  --color-white: #fff;    
}

* {
  /* font-family: "Open Sans", sans-serif; */

  text-decoration: none; 

  border: 0;
  margin: 0;
  padding: 0;

  /* ü¶ÑmarginÏùò Í∏∞Ï§ÄÏÑ†Ïù¥ borderÎ•º Í∏∞Ï§ÄÏúºÎ°ú  ÎßåÎì§Ïñ¥Ïßê */
  box-sizing: border-box;

  /* ü¶Ñ  Î™®Îì† transitionÏóê Í≥µÌÜµÏúºÎ°ú ÎÑ£Ïùå. ÏùºÏùºÌûà Í∞Å elementÎßàÎã§ transitionÎÑ£ÏùÑ ÌïÑÏöîÏóÜÏùå*/
  transition: all 0.2s linear;   

  /* üëâgoogle font import */
  font-family: 'Preahvihear', sans-serif;

  /* scrollÎ∂ÄÎìúÎüΩÍ≤å ( + html : <a href="#~~~~"></a> Ïç®ÏïºÌï®) */
  scroll-behavior: smooth;

  /* ü¶Ñ tip: ÏΩîÎî©Ìé∏Ìï®  */
  overflow-x: hidden;

  /* ü¶Ñ ???   */
  scroll-padding-top:  6rem;  
}


