/* üçÄmyBtn, button  
  btn-generator Î≥¥Í∏∞Ï¢ãÍ≤å Ï†ïÎ¶¨Ìïú Í≤É 
  https://www.bestcssbuttongenerator.com/#/13
*/
.myButton {
  /* Î™®Îì† Î≤ÑÌäº width ÎòëÍ∞ôÏùÄ ÌÅ¨Í∏∞Î°ú ÏÑ§Ï†ï */
  width: 9rem;
	padding:6px 24px;

	box-shadow:inset 0px 1px 0px 0px #ffffff;
	background:linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
	background-color:#f9f9f9;
	border-radius:6px;
	border:1px solid #dcdcdc;
	display:inline-block;
	cursor:pointer;
	color:#666666;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;
  
  text-align: center; 
}

/* hover-focus-activeÏàúÏÑúÎ°ú ÌïòÎ©¥ ÏóêÎü¨ ÏïàÎÇ® */
.myButton:hover {
	background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
	background-color:#e9e9e9;
}
.myButton:active {
	position:relative;
	top:1px;
}

/*üçâtransparent button  */
.myBtn-empty {
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

/* üçâicon Btn */
.iconBtn {

