}  

.iconBtn :hover {
	background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
	background-color:#e9e9e9;
}
.iconBtn :active {
	position:relative;
	top:1px;
}


/* T: keyframes */

.div h1 span:nth-child(1) {
  animation: drop 30s 1s infinite;
}
.div h1 span:nth-child(2) {
  animation: drop 30s 5s infinite;
}
.div h1 span:nth-child(3) {
  animation: drop 30s 10s infinite;
}

@keyframes drop {
  0% {
    transform: translateY(0);
  }

  20% {
    transform: translatey(-50%);
  }

  100% {
    transform: translateY(0);
  }
}



/* ü¶Ñgrid 1 -me */
/* html :  
    <form id="parent">
        <div class="formLine left grid-a">
    </form>
  */

  #parent {
    display: grid;
  
    grid-template-areas:
      "a b"
      "c d"
      ". e"
      ". f";
  
    /* 
      üçÄgrid : ÏïÑÎ¨¥ ÏïåÌååÎ≤≥ ÎÑ£Í≥†, ÏÑ§Ï†ïÏïàÌï¥ÎèÑ Í∑∏ÎåÄÎ°ú Í∑∏Î¶¨Îìú ÎßåÎì§Ïñ¥Ïßê 
        grid-template-areas:
        "o p q  "
        "s t a  "
        "u v c  "
        "w x e ";
      */
  
    gap: 2.5%;
  
    /*üçÄgrid: img Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ in the container */
    justify-items: center;
    align-items: center;
  }
  
  .grid-a {
    grid-area: a;
  }
  .grid-b {
    grid-area: b;
  }
  .grid-c {
    grid-area: c;
  }
  
  /* ü¶Ñgrid 2 : grid-template-columns  */
  .grid{
    display: grid;
    grid-template-areas: 
    'header'
    'main'
    'footer';  
    grid-template-columns: 1fr;
    grid-template-rows: 5rem 1fr 5rem;      /* üçö 1fr : Îã§Î•∏ gridÍ∞Ä Ï∞®ÏßÄÌïòÍ≥† ÎÇ®ÏùÄ Í≥µÍ∞Ñ Ï∞®ÏßÄ */
  
    grid-gap: 20px 5px;
  }
  
  
  /* ü¶Ñgrid 3 : grid-template-columns  */
  .grid{
    display: grid;
    /* 1frÏùÑ 4Î≤àÎ∞òÎ≥µ */
    grid-template-columns: repeat(4,1fr);
    /* grid-template-columns: repeat(4,25%); */
    grid-gap: 20px 5px;
  }
  
  /* ü¶Ñgrid 4 */
  /*
    columns 16Ïπ∏ (lineÏúºÎ°ú Îî∞ÏßÄÎ©¥ 1~17)
    row 6Ïπ∏ (lineÏúºÎ°ú Îî∞ÏßÄÎ©¥ 1~7)ÎßåÎì¨
    */
  .parent {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    grid-template-rows: repeat(6, 6rem);
    grid-row-gap: 4rem;
  }
  
  /* column line 7~11, row line 2~6 ÏòÅÏó≠ÏùÑÏ∞®ÏßÄÌï®*/
  .about-us-img-wrapper {
    grid-column: 7 / 11;
    grid-row: 2 / 6;
    width: 100%;
  }
  
  .service:nth-child(1) {
    grid-column: 4 / 7;
    grid-row: 1 / 3;
  }
  
  
